body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
.navbar{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width:100%;
  background-color: black;
  padding: 3px;
  position: fixed;
  z-index: 1;
}
.cartImage{
  color:white;
  font-size:20px;
  position: relative;
}
.cartLength{
  width:25px;
  height: 25px;
  background-color: green;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3px;
  position: absolute;
  top:-15px;
  right:-20px;
  border-radius: 50%;
  font-weight: 700;
  animation: moveUpDown 2s ease-in-out infinite; 

}
@keyframes moveUpDown {
  0%, 100% {
    transform: translate(0);
  }
  50% {
    transform: translate(-20px); 
  }
}
.navbarText{
  color:white;
  cursor: pointer;
}
.navbarText:hover{
  color:azure;
}
.navbarCart{
  display: flex;
  gap: 15px;
  cursor: pointer;
}
.navbarCart:hover{
  color:azure;
}
.navbarImage{
  width:150px;
  height:50px;
}
.spinner {
  width: 56px;
  height: 56px;
  border-radius: 50%;
  background: conic-gradient(#0000 10%,#47b2ff);
  -webkit-mask: radial-gradient(farthest-side,#0000 calc(100% - 9px),#000 0);
  animation: spinner-zp9dbg 1s infinite linear;
}

@keyframes spinner-zp9dbg {
  to {
     transform: rotate(1turn);
  }
}
 .home{
   width:100%;
   min-height: 100vh;
   max-height: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
}
.Cards{
  width:60%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-top: 70px;
}
.card{
  height:280px;
  width:21%;
  margin:10px;
  box-shadow: 0 2px 3px #333;
  padding: 2px;
  position: relative;
  border-radius: 10px;
  transition: transform 0.3s ease-in-out;
}
.card:hover{
  transform: scale(1.08);
  cursor: pointer;
  box-shadow: 0 0 20px #333;
}
.card:hover .ATCButton{
  background-color: #333;
  border: white;
  color:white;

}
.title{
  font-weight: 700;
  margin-top: 5px;
}
.description{
  font-size: small;
  color: grey;
  margin-top:10px ;
  margin-bottom: 20px;
}
.productImage{
  height:120px;
  width:120px;
  
}

.cardBottomSection{
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  width:100%;
  bottom: 10px;
  padding: 0 5px;
}
.price{
  color:green;
  font-weight: 700;
  font-size: 14px;
}
.ATCButton{
  border: 2px solid black;
  border-radius: 20px;
  font-size: 9px;
  font-weight: 700;
  padding: 5px 10px;
  background-color: white;
  cursor: pointer;
}
.active{
  color:white;
  text-decoration: none;
  font-weight: 800;
}
a{
  text-decoration: none;
  color: white;
}
.cart{
  width:100%;
  min-height: 100vh;
  max-height:100%;
  display: flex;
  justify-content: center;
  position: relative;
  top:60px;
}
.cartPage{
  
  width:80%;
  display: flex;
  position: relative;
  padding:10px;
  
}
.products{
  width:70%;
}
.prodCard{
  display: flex;
  justify-content: center;
  align-items: center;
  height:300px;
  padding: 20px;
  border-bottom: 2px solid #333;
}
.totalProduct{
  position: absolute;
  top:8vh;
  right:10vw;
}
.prodImage{
  width:250px;
  height:250px;
}
.bottomPart{
  display: flex;
  justify-content: space-between;
  padding: 20px;
  margin-top: 10px;
}
.prodDetail{
  margin-left: 20px;
  padding: 20px;
}
.delButton{
  width:30px;
  height:30px;
  border-radius: 50%;
  background-color: rgba(255, 0, 0, 0.546);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  box-shadow: 0 2px 3px #333;
}
.yourCart{
  font-size: 20px;
  font-weight: 900;
  color: green;
}
.summary{
  font-size: 30px;
  font-weight: 900;
  color: green;
}
.totalItems{
  font-weight: 700;
}
.totalAmounts{
  font-weight: 700;
}
.emptyCart{
  width:100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap:5px;
}
.emptyCartText{
  font-weight: 700;
}
.shopNow{
  border:none;
  background-color: green;
  color:white;
  padding: 10px 20px;
  border-radius: 10px;
  font-weight: 700;
}
@media(max-width:1200px){
  .Cards{
    width:80%;
  }
}
@media(max-width:930px){
  .Cards{
    width:100%;
  }
}
@media(max-width:800px){
  .card{
    width:30%;
  }
}
@media(max-width:550px){
  .card{
    width:80%;
  }
}
@media(max-width:1000px){
  .cartPage{
    width:100%;
  }
}
@media(max-width:800px){
   .products{
    width:70%;
    margin-right: 20px;
    
   }
   .prodCard{
    height:350px;
   }
   .totalProduct{
     right:1vh;
     
   }
   .prodImage{
    width:180px;
    height:180px;
  }
}
@media(max-width:630px){
  .prodImage{
    width:150px;
    height:150px;
  }
}
@media(max-width:600px){
  .prodImage{
    width:100px;
    height:100px;
  }
  .products{
    width:70%;
   }
   .prodCard{
    height:400px;
   }

}
@media(max-width:500px){
  .cartPage{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .totalProduct{
    position: relative;
    background-color: #1C1B1B;
    width:200px;
    padding-bottom: 20px;
    
  }
  .bottomPart{
    width:150px;
  }
  .prodImage{
    margin-left: 10px;
  }
  .prodImage{
    width:150px;
    height:150px;
  }
}
.dark{
  background-color: #1c1b1b;
}
.themeChanger{
  width:30px;
  height:30px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-weight: 800;
}
.darkTitle{
  color:white;
}
.darkDescription{
  color:rgba(255, 255, 255, 0.797);
}
.darkCard{
  box-shadow: 0 0 5px #757474;
  background-color: #323131;
  border-bottom: 2px solid #898888;
}
.darkCard:hover{
  box-shadow: 0 0 10px #757474;
}
.darkATCButton{
  background-color: #333;
  color: white;
  border: 1px solid white;
}
.darkCard:hover .darkATCButton{
  background-color: white;
  color:#333;
  border: 1px solid black;
}
.darkTotalItems{
  color:white;
}
.darkPrice{
  color:rgb(155, 241, 27);
}
.darkYourCart{
  color:rgb(155, 241, 27);
}
.darkCart{
  width:100%;
  height:100%;
  background-color: #1C1B1B;
}
